import React, { useContext } from "react";
import deviceImg from "./DeviceImage/1.png";
import { IoCheckmarkSharp } from "react-icons/io5";
import { PiShieldWarning } from "react-icons/pi";
import { CustomContext } from "@/Context/UseCustomContext";
export default function DevicesTab() {
  const { elderlyDetails } = useContext(CustomContext);
  return (
    <>
      <div className="flex flex-col gap-4 mt-6">
        <div className="flex items-center justify-between">
          <h1 className="text-[24px] font-bold text-[#1B2559]">Devices</h1>
        </div>
        <div
          id="devices"
          className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-3 gap-6"
        >
          {elderlyDetails?.rooms
            ?.filter((tab) => tab?.device_no)
            .map((device) => (
              <DeviceCard key={device.id} device={device} />
            ))}
        </div>
      </div>
    </>
  );
}

const DeviceCard = ({ device }) => {
  return (
    <div className="flex flex-col gap-8 bg-white rounded-xl p-4">
      <div className="flex items-center gap-4">
        <div id="deviceImg">
          <img src={deviceImg} alt="device" width={84} height={84} />
        </div>
        <div className="flex flex-col">
          <h1 className="text-[22px] font-semibold text-[#1B2559]">
            {device?.name}
          </h1>
          <div id="status" className="flex items-center gap-2">
            <div
              id="dot"
              className={` ${
                device?.is_device_bind ? "bg-[#00c42e]" : "bg-[#c42626]"
              } rounded-full animate-pulse w-[8px] h-[8px]`}
            ></div>
            <p className="text-[16px] font-semibold text-primary capitalize">
              {device?.is_device_bind ? "Online" : "Offline"}
            </p>
          </div>
        </div>
      </div>
      <div id="issue" className="flex items-start justify-start gap-2 w-[60%]">
        <div id="icon">
          {device.issueStatus === "0" ? (
            <IoCheckmarkSharp size={18} color="#00c42e" />
          ) : (
            <PiShieldWarning size={18} color="#ff9f0d" />
          )}
        </div>
        <p className="text-[15px] font-medium text-[#1B2559] capitalize">
          {device.issue || "--"}
        </p>
      </div>
    </div>
  );
};

const deviceList = [
  {
    id: 1,
    name: "Bedroom",
    status: "active",
    issueStatus: "0",
    issue: "No issues Detected",
  },
  {
    id: 2,
    name: "Kitchen",
    status: "active",
    issueStatus: "1",
    issue: "No Objects Detecting",
  },
];
